import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import {
    ModalContent,
    CloseButton,
    OrderDetailItem,
    Label,
    Value,
    ProductImage,
    ModalHeader,
    ModalSection,
    SectionTitle,
    SectionContent,
    Info,
    InlineContainer,
    StatusDropdown,
    Badge,
} from "./OrderModalStyles";
import { useUpdateOrderStatus } from "../../hooks/useOrder";

Modal.setAppElement("#root");

const OrderModal = ({ isOpen, onRequestClose, order, isAdmin }) => {
    const { mutate: updateOrderStatus } = useUpdateOrderStatus();
    const [selectedStatus, setSelectedStatus] = useState(order?.status);

    useEffect(() => {
        setSelectedStatus(order?.status);
    }, [order]);

    const handleStatusChange = (event) => {
        setSelectedStatus(event.target.value);
        updateOrderStatus({ id: order._id, status: event.target.value });
    };

    const calculateTotalPrice = (items) => {
        return items
            ?.reduce((total, item) => total + item?.price * item?.qty, 0)
            .toFixed(2);
    };

    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            overlayClassName="modal-overlay"
            className="modal-content"
        >
            <ModalContent>
                <ModalHeader>
                    <CloseButton onClick={onRequestClose}></CloseButton>
                </ModalHeader>
                <ModalSection>
                    <InlineContainer>
                        <SectionTitle> </SectionTitle>{" "}
                        {isAdmin ? (
                            <StatusDropdown
                                value={selectedStatus}
                                onChange={handleStatusChange}
                            >
                                {" "}
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </StatusDropdown>
                        ) : (
                            <Badge className={order?.status}>
                                {order?.status}
                            </Badge>
                        )}
                    </InlineContainer>
                    <SectionContent>
                        <OrderDetailItem>
                            <Label> :</Label>
                            <Value>{order?.orderNum}</Value>
                        </OrderDetailItem>
                        <OrderDetailItem>
                            <Label> :</Label>
                            <Value>
                                {new Date(
                                    order?.createdAt
                                ).toLocaleDateString()}
                            </Value>
                        </OrderDetailItem>
                    </SectionContent>
                </ModalSection>
                <ModalSection>
                    <SectionTitle> </SectionTitle>
                    <SectionContent>
                        {order?.items.map((item) => (
                            <OrderDetailItem key={item?._id}>
                                <ProductImage
                                    src={item?.productId.image}
                                    alt={item?.productId.name}
                                />
                                <Info>
                                    <Label> :</Label>
                                    <Value>{item?.productId.name}</Value>
                                    <Label>:</Label>
                                    <Value>${item?.price}</Value>
                                    <Label>:</Label>
                                    <Value>{item?.qty}</Value>
                                    <Label>:</Label>
                                    <Value>{item?.size}</Value>
                                </Info>
                            </OrderDetailItem>
                        ))}
                    </SectionContent>
                </ModalSection>
                <ModalSection>
                    <SectionTitle> </SectionTitle>
                    <SectionContent>
                        <OrderDetailItem>
                            <Label>:</Label>
                            <Value>{order?.contact.Name}</Value>
                        </OrderDetailItem>
                        <OrderDetailItem>
                            <Label>:</Label>
                            <Value>{order?.contact.contact}</Value>
                        </OrderDetailItem>
                        <OrderDetailItem>
                            <Label>:</Label>
                            <Value>{order?.shipTo.address}</Value>
                        </OrderDetailItem>
                        <OrderDetailItem>
                            <Label> :</Label>
                            <Value>{order?.shipTo.memo}</Value>
                        </OrderDetailItem>
                    </SectionContent>
                </ModalSection>
                <ModalSection>
                    <SectionTitle> </SectionTitle>
                    <SectionContent>
                        <OrderDetailItem>
                            <Label> :</Label>
                            <Value>${calculateTotalPrice(order?.items)}</Value>
                        </OrderDetailItem>
                    </SectionContent>
                </ModalSection>
            </ModalContent>
        </Modal>
    );
};

export default OrderModal;
