import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import {
    ModalContainer,
    CloseButton,
    Form,
    FormGroup,
    Label,
    Input,
    Textarea,
    Select,
    Button,
} from "./ProductModalStyles";
import { brandCategories, categories } from "./categories";
import { useAddProduct } from "../../hooks/useGetProduct";
import CloudinaryUploadWidget from "../../components/CloudinaryUploadWidget/CloudinaryUploadWidget";

Modal.setAppElement("#root");

const ProductModal = ({ isOpen, onRequestClose, notify }) => {
    const [productName, setProductName] = useState("");
    const [productPrice, setProductPrice] = useState("");
    const [productDescription, setProductDescription] = useState("");
    const [brand, setBrand] = useState("");
    const [bigCategory, setBigCategory] = useState("");
    const [mainCategory, setMainCategory] = useState("");
    const [subCategory, setSubCategory] = useState("");
    const [subSubCategory, setSubSubCategory] = useState("");
    const [image, setImage] = useState("");
    const [sku, setSku] = useState("");
    const [color, setColor] = useState("");
    const [status, setStatus] = useState("active");
    const [stock, setStock] = useState([]);

    const mutation = useAddProduct();

    const handleImageUpload = (url) => {
        setImage(url);
    };

    const handleImageUrlChange = (e) => {
        setImage(e.target.value);
    };

    useEffect(() => {
        if (mainCategory === "clothes") {
            setStock(
                ["XS", "S", "M", "L", "XL"].map((size) => ({
                    size,
                    quantity: 0,
                }))
            );
        } else if (mainCategory === "bags" || mainCategory === "accessories") {
            setStock([{ size: "Free", quantity: 0 }]);
        } else if (mainCategory === "shoes") {
            setStock(
                ["220", "230", "240", "250", "260", "270", "280", "290"].map(
                    (size) => ({ size, quantity: 0 })
                )
            );
        } else {
            setStock([]);
        }
    }, [mainCategory]);

    const handleStockChange = (index, quantity) => {
        const newStock = [...stock];
        newStock[index].quantity = quantity;
        setStock(newStock);
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        if (
            !productName ||
            !productPrice ||
            !bigCategory ||
            !mainCategory ||
            !subCategory ||
            !image
        ) {
            alert("    .");
            return;
        }

        mutation.mutate(
            {
                sku,
                name: productName,
                brand,
                bigCategory,
                category: {
                    main: mainCategory,
                    sub: subCategory,
                    subSub: subSubCategory,
                },
                image,
                description: productDescription,
                price: parseFloat(productPrice), //  
                stock: stock.reduce((acc, item) => {
                    acc[item.size] = item.quantity;
                    return acc;
                }, {}),
                color,
                status,
            },
            {
                onSuccess: () => {
                    setProductName("");
                    setProductPrice("");
                    setProductDescription("");
                    setBrand("");
                    setBigCategory("");
                    setMainCategory("");
                    setSubCategory("");
                    setSubSubCategory("");
                    setImage("");
                    setSku("");
                    setColor("");
                    setStatus("active");
                    setStock([]);
                    onRequestClose();
                    notify(" .");
                },
                onError: (error) => {
                    console.error("Product creation failed:", error);
                },
            }
        );
    };

    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            contentLabel="Product Modal"
            style={{ overlay: { zIndex: 1000 } }}
        >
            <ModalContainer>
                <CloseButton onClick={onRequestClose}></CloseButton>
                <h2> </h2>
                <Form onSubmit={handleSubmit}>
                    <FormGroup>
                        <Label>SKU:</Label>
                        <Input
                            type="text"
                            value={sku}
                            onChange={(e) => setSku(e.target.value)}
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label>:</Label>
                        <Input
                            type="text"
                            value={productName}
                            onChange={(e) => setProductName(e.target.value)}
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label>:</Label>
                        <Select
                            value={brand}
                            onChange={(e) => setBrand(e.target.value)}
                        >
                            <option value=""> </option>
                            {brandCategories.map((brand) => (
                                <option key={brand} value={brand}>
                                    {brand}
                                </option>
                            ))}
                        </Select>
                    </FormGroup>
                    <FormGroup>
                        <Label>:</Label>
                        <Select
                            value={bigCategory}
                            onChange={(e) => setBigCategory(e.target.value)}
                        >
                            <option value=""> </option>
                            {Object.keys(categories).map((main) => (
                                <option key={main} value={main}>
                                    {main}
                                </option>
                            ))}
                        </Select>
                    </FormGroup>
                    {bigCategory && (
                        <FormGroup>
                            <Label>:</Label>
                            <Select
                                value={mainCategory}
                                onChange={(e) =>
                                    setMainCategory(e.target.value)
                                }
                            >
                                <option value=""> </option>
                                {Object.keys(categories[bigCategory]).map(
                                    (sub) => (
                                        <option key={sub} value={sub}>
                                            {sub}
                                        </option>
                                    )
                                )}
                            </Select>
                        </FormGroup>
                    )}
                    {mainCategory && (
                        <FormGroup>
                            <Label>:</Label>
                            <Select
                                value={subCategory}
                                onChange={(e) => setSubCategory(e.target.value)}
                            >
                                <option value=""> </option>
                                {categories[bigCategory][mainCategory].map(
                                    (subSub) => (
                                        <option key={subSub} value={subSub}>
                                            {subSub}
                                        </option>
                                    )
                                )}
                            </Select>
                        </FormGroup>
                    )}
                    <FormGroup>
                        <Label>:</Label>
                        <Input
                            type="text"
                            value={productPrice}
                            onChange={(e) => setProductPrice(e.target.value)}
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label>:</Label>
                        <Textarea
                            value={productDescription}
                            onChange={(e) =>
                                setProductDescription(e.target.value)
                            }
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label>:</Label>
                        <Input
                            type="text"
                            value={color}
                            onChange={(e) => setColor(e.target.value)}
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label>:</Label>
                        <Select
                            value={status}
                            onChange={(e) => setStatus(e.target.value)}
                        >
                            <option value="active">Active</option>
                            <option value="inactive">Inactive</option>
                        </Select>
                    </FormGroup>
                    <FormGroup>
                        <Label>:</Label>
                        {stock.map((stockItem, index) => (
                            <FormGroup key={stockItem.size}>
                                <Label>{stockItem.size}:</Label>
                                <Input
                                    type="number"
                                    value={stockItem.quantity}
                                    onChange={(e) =>
                                        handleStockChange(index, e.target.value)
                                    }
                                />
                            </FormGroup>
                        ))}
                    </FormGroup>
                    <FormGroup>
                        <Label> :</Label>
                        <CloudinaryUploadWidget
                            uploadImage={handleImageUpload}
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label>  URL:</Label>
                        <Input
                            type="text"
                            value={image}
                            onChange={handleImageUrlChange}
                        />
                        {image && (
                            <img
                                id="uploadedimage"
                                src={image}
                                alt="Product"
                                style={{ width: "100px", height: "100px" }}
                            />
                        )}
                    </FormGroup>
                    <Button type="submit"></Button>
                </Form>
            </ModalContainer>
        </Modal>
    );
};

export default ProductModal;
