import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { prevStep, nextStep } from "../../action/SignUpAction";
import { useSignUpMutation } from "../../hooks/useSignUpMutation";
import {
    Title,
    CheckboxLabel,
    Line,
    Input,
    Button,
    LinkButton,
    Error,
    RadioGroup,
    StyledRadioInput,
    RadioLabel,
} from "./SignUpFormStyles";

const SignUpForm = () => {
    const step = useSelector((state) => state.step.step);
    const dispatch = useDispatch();
    const [allChecked, setAllChecked] = useState(false);
    const [termsChecked, setTermsChecked] = useState({
        age: false,
        terms: false,
        privacy: false,
        marketing: false,
        ads: false,
    });
    const [role, setRole] = useState("user");

    useEffect(() => {
        const allChecked = Object.values(termsChecked).every(Boolean);
        setAllChecked(allChecked);
    }, [termsChecked]);

    const handleAllCheck = () => {
        const newValue = !allChecked;
        setAllChecked(newValue);
        setTermsChecked({
            age: newValue,
            terms: newValue,
            privacy: newValue,
            marketing: newValue,
            ads: newValue,
        });
    };

    const handleIndividualCheck = (name) => {
        setTermsChecked((prev) => ({
            ...prev,
            [name]: !prev[name],
        }));
    };

    const canProceed =
        termsChecked.age && termsChecked.terms && termsChecked.privacy;

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [pw, setPw] = useState("");
    const [rePw, setRePw] = useState("");
    const [error, setError] = useState("");

    const isFormFilled = () => {
        return name && email && pw && rePw;
    };

    const { mutate, error: mutationError } = useSignUpMutation();

    const handleSubmit = (e) => {
        e.preventDefault();
        if (pw !== rePw) {
            setError("  .  .");
            return;
        }
        const newUser = { name, email, password: pw, role };
        mutate(newUser);
    };

    useEffect(() => {
        if (mutationError) {
            setError(mutationError);
        }
    }, [mutationError]);

    return (
        <form>
            {step === 1 && (
                <>
                    <Title> </Title>
                    <CheckboxLabel>
                        <input
                            type="checkbox"
                            checked={allChecked}
                            onChange={handleAllCheck}
                        />
                        <span>  (  )</span>
                    </CheckboxLabel>
                    <Line></Line>
                    <CheckboxLabel>
                        <input
                            type="checkbox"
                            checked={termsChecked.age}
                            onChange={() => handleIndividualCheck("age")}
                        />
                        <span>[]  14 </span>
                    </CheckboxLabel>
                    <CheckboxLabel>
                        <input
                            type="checkbox"
                            checked={termsChecked.terms}
                            onChange={() => handleIndividualCheck("terms")}
                        />
                        <span>[]  </span>
                        <a href="#"></a>
                    </CheckboxLabel>
                    <CheckboxLabel>
                        <input
                            type="checkbox"
                            checked={termsChecked.privacy}
                            onChange={() => handleIndividualCheck("privacy")}
                        />
                        <span>[]   </span>
                        <a href="#"></a>
                    </CheckboxLabel>
                    <CheckboxLabel>
                        <input
                            type="checkbox"
                            checked={termsChecked.marketing}
                            onChange={() => handleIndividualCheck("marketing")}
                        />
                        <span>[]    </span>
                        <a href="#"></a>
                    </CheckboxLabel>
                    <CheckboxLabel>
                        <input
                            type="checkbox"
                            checked={termsChecked.ads}
                            onChange={() => handleIndividualCheck("ads")}
                        />
                        <span>[]    </span>
                        <a href="#"></a>
                    </CheckboxLabel>
                    <Button
                        type="button"
                        onClick={() => dispatch(nextStep())}
                        disabled={!canProceed}
                    >
                        
                    </Button>
                </>
            )}

            {step === 2 && (
                <>
                    <Button type="button" onClick={() => dispatch(prevStep())}>
                         
                    </Button>
                    <Title> </Title>
                    <Input
                        type="text"
                        placeholder=""
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                    />
                    <Title>   </Title>
                    <Input
                        type="email"
                        placeholder=""
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />
                    <Title> </Title>
                    <Input
                        type="password"
                        placeholder=""
                        value={pw}
                        onChange={(e) => setPw(e.target.value)}
                    />
                    <Title>    </Title>
                    <Input
                        type="password"
                        placeholder=" "
                        value={rePw}
                        onChange={(e) => setRePw(e.target.value)}
                    />
                    <Title>    </Title>
                    <RadioGroup>
                        <StyledRadioInput
                            type="radio"
                            id="user"
                            name="role"
                            value="user"
                            checked={role === "user"}
                            onChange={(e) => setRole(e.target.value)}
                        />
                        <RadioLabel htmlFor="user"></RadioLabel>
                    </RadioGroup>
                    <RadioGroup>
                        <StyledRadioInput
                            type="radio"
                            id="admin"
                            name="role"
                            value="admin"
                            checked={role === "admin"}
                            onChange={(e) => setRole(e.target.value)}
                        />
                        <RadioLabel htmlFor="admin"></RadioLabel>
                    </RadioGroup>
                    {error && <Error>{error}</Error>}
                    <Button
                        type="button"
                        onClick={handleSubmit}
                        disabled={!isFormFilled()}
                    >
                        
                    </Button>
                </>
            )}
            {step === 3 && (
                <>
                    <Title>{name}  .</Title>
                    <LinkButton href="/"> </LinkButton>
                </>
            )}
        </form>
    );
};

export default SignUpForm;
