import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useGetCart } from "../../hooks/useCart";
import { useAddToOrder } from "../../hooks/useOrder";
import PaymentForm from "../../components/PaymentForm/PaymentForm";
import {
    Container,
    Form,
    InputGroup,
    Label,
    Input,
    Button,
    Summary,
    SummaryItem,
    GridContainer,
    ErrorMessage,
} from "./PaymentpageStyles";
import PostCode from "../../components/PostCode/PostCode";

const PaymentPage = () => {
    const navigate = useNavigate();
    const { data: cartList, isLoading, error } = useGetCart();
    const addToOrder = useAddToOrder(navigate);
    const [address, setAddress] = useState("");
    const [zip, setZip] = useState("");

    const [cardValue, setCardValue] = useState({
        cvc: "",
        expiry: "",
        focus: "",
        name: "",
        number: "",
    });
    const [shipInfo, setShipInfo] = useState({
        Name: "",
        address: "",
        detail: "",
        zip: "",
        contact: "",
    });
    const [totalPrice, setTotalPrice] = useState(0);
    const [errors, setErrors] = useState({});
    const [isFormValid, setIsFormValid] = useState(false);

    useEffect(() => {
        if (cartList && cartList.length > 0) {
            const total = cartList.reduce(
                (acc, item) => acc + item.productId.price * item.qty,
                0
            );
            setTotalPrice(total);
        }
    }, [cartList]);

    useEffect(() => {
        setShipInfo((prevInfo) => ({
            ...prevInfo,
            address,
            zip,
        }));
    }, [address, zip]);

    useEffect(() => {
        const isShipInfoValid = Object.values(shipInfo).every(
            (field) => field.trim() !== ""
        );
        const isCardValueValid = Object.values(cardValue).every(
            (field) => field.trim() !== ""
        );
        setIsFormValid(isShipInfoValid && isCardValueValid);
    }, [shipInfo, cardValue]);

    const handleSubmit = (event) => {
        event.preventDefault();
        const { Name, address, detail, zip, contact } = shipInfo;
        const newErrors = {};

        if (!Name) newErrors.Name = " .";
        if (!address) newErrors.address = " .";
        if (!detail) newErrors.detail = " .";
        if (!zip) newErrors.zip = " .";
        if (!contact) newErrors.contact = " .";
        if (!cardValue.name) newErrors.cardName = "  .";
        if (!cardValue.number)
            newErrors.cardNumber = "  .";
        if (!cardValue.expiry)
            newErrors.cardExpiry = "  .";
        if (!cardValue.cvc) newErrors.cardCvc = " CVC .";

        if (Object.keys(newErrors).length > 0) {
            setErrors(newErrors);
        } else {
            const data = {
                totalPrice,
                shipTo: { address, detail, zip },
                contact: { Name, lastName: "", contact },
                orderList: cartList.map((item) => ({
                    productId: item.productId._id,
                    price: item.productId.price,
                    qty: item.qty,
                    size: item.size,
                })),
            };
            addToOrder.mutate(data);
        }
    };

    const handleFormChange = (event) => {
        const { name, value } = event.target;
        setShipInfo({ ...shipInfo, [name]: value });
        setErrors({ ...errors, [name]: "" });
    };

    const handlePaymentInfoChange = (event) => {
        const { name, value } = event.target;
        setCardValue({ ...cardValue, [name]: value });
        setErrors({ ...errors, [name]: "" });
    };

    const handleInputFocus = (e) => {
        setCardValue({ ...cardValue, focus: e.target.name });
    };

    if (isLoading) return <div>Loading...</div>;
    if (error) return <div>Error loading cart items</div>;

    return (
        <Container>
            <GridContainer>
                <Form onSubmit={handleSubmit}>
                    <h2> </h2>
                    <InputGroup>
                        <Label></Label>
                        <Input
                            type="text"
                            name="Name"
                            value={shipInfo.Name}
                            onChange={handleFormChange}
                            required
                        />
                        {errors.Name && (
                            <ErrorMessage>{errors.Name}</ErrorMessage>
                        )}
                    </InputGroup>
                    <InputGroup>
                        <Label></Label>
                        <Input
                            type="text"
                            name="contact"
                            value={shipInfo.contact}
                            onChange={handleFormChange}
                            required
                        />
                        {errors.contact && (
                            <ErrorMessage>{errors.contact}</ErrorMessage>
                        )}
                    </InputGroup>
                    <InputGroup>
                        <Label>
                            
                            <PostCode setAddress={setAddress} setZip={setZip} />
                        </Label>

                        <Input
                            type="text"
                            name="zip"
                            value={shipInfo.zip}
                            onChange={handleFormChange}
                            required
                            readOnly
                        />
                        {errors.zip && (
                            <ErrorMessage>{errors.zip}</ErrorMessage>
                        )}
                    </InputGroup>
                    <InputGroup>
                        <Label></Label>
                        <Input
                            type="text"
                            name="address"
                            value={shipInfo.address}
                            onChange={handleFormChange}
                            required
                            readOnly
                        />
                        {errors.address && (
                            <ErrorMessage>{errors.address}</ErrorMessage>
                        )}
                    </InputGroup>
                    <InputGroup>
                        <Label></Label>
                        <Input
                            type="text"
                            name="detail"
                            value={shipInfo.detail}
                            onChange={handleFormChange}
                            required
                        />
                        {errors.detail && (
                            <ErrorMessage>{errors.detail}</ErrorMessage>
                        )}
                    </InputGroup>
                    <Button type="submit" disabled={!isFormValid}>
                        
                    </Button>
                </Form>

                <Summary>
                    <h2>Order Summary</h2>
                    {cartList.map((item) => (
                        <SummaryItem key={item.productId._id}>
                            <span>{item.productId.name}</span>
                            <span>
                                {item.qty} x ${item.productId.price} ={" "}
                                {item.qty * item.productId.price}
                            </span>
                        </SummaryItem>
                    ))}
                    <SummaryItem>
                        <span>  </span>
                        <span>${totalPrice}</span>{" "}
                    </SummaryItem>
                </Summary>
            </GridContainer>
            <PaymentForm
                handleInputFocus={handleInputFocus}
                cardValue={cardValue}
                handlePaymentInfoChange={handlePaymentInfoChange}
            />
        </Container>
    );
};

export default PaymentPage;
